<template>
  <div class="container">
    <el-card class="box-card">
      <questions-search
        :questionsSData="questionsSData"
        @submitForm="submitForm"
      ></questions-search>
      <el-alert
        :closable="false"
        :title="'数据一共 ' + total + ' 条'"
        show-icon
        type="info"
      >
      </el-alert>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column label="试题编号" prop="number"></el-table-column>
        <el-table-column label="学科" prop="subject"></el-table-column>
        <el-table-column label="目录" prop="catalog"></el-table-column>
        <el-table-column label="题型">
          <template slot-scope="scope">
            <p v-if="scope.row.questionType === '1'">单选</p>
            <p v-else-if="scope.row.questionType === '2'">多选</p>
            <p v-else>测试</p>
          </template>
        </el-table-column>
        <el-table-column label="提干">
          <template slot-scope="scope">
            <p v-html="scope.row.question"></p>
          </template>
        </el-table-column>
        <el-table-column label="录入时间">
          <template slot-scope="scope">
            {{
              scope.row.addDate | parseTimeByString('{y}-{m}-{d} {h}:{i}:{s}')
            }}
          </template>
        </el-table-column>
        <el-table-column label="难度">
          <template slot-scope="scope">
            <p v-if="scope.row.difficulty === '1'">简单</p>
            <p v-else-if="scope.row.difficulty === '2'">一般</p>
            <p v-else>困难</p>
          </template>
        </el-table-column>
        <el-table-column label="录入人" prop="creator"></el-table-column>
        <el-table-column label="操作" width="230">
          <template slot-scope="scope">
            <!--            预览-->
            <el-button
              circle
              icon="el-icon-view"
              plain
              type="primary"
              @click="getDetail(scope.row)"
            ></el-button>
            <!--            修改-->
            <el-button
              circle
              icon="el-icon-edit"
              plain
              type="success"
              @click="$router.push('/questions/new?id=' + scope.row.id)"
            ></el-button>
            <!--            删除-->
            <el-button
              circle
              icon="el-icon-delete"
              plain
              type="danger"
              @click="deleteQuestionsById(scope.row)"
            ></el-button>
            <!--            加入精选-->
            <el-button
              circle
              icon="el-icon-check"
              plain
              type="warning"
              @click="patchChoiceAdd(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        :visible.sync="dialogVisible"
        title="题目预览"
        width="900px"
        @close="videos = false"
      >
        <questions-preview
          v-model="videos"
          :dialogData="dialogData"
        ></questions-preview>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogVisible = false"
            >关 闭</el-button
          >
        </span>
      </el-dialog>
      <el-pagination
        :current-page="questionsSData.page"
        :page-size="questionsSData.pagesize"
        :page-sizes="[5, 10, 20, 40]"
        :total="total"
        background
        layout=" prev, pager, next,sizes, jumper"
        style="float: right;margin-bottom: 20px"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import questionsSearch from '@/components/questionsSearch'
import questionsPreview from '@/module-hmmm/components/questions-preview'
import { choiceAdd, detail, list, remove } from '@/api/hmmm/questions'
// import * as dayjs from 'dayjs'

export default {
  data () {
    return {
      tableData: [],
      questionsSData: {
        subjectID: null, // 学科
        difficulty: null, // 难度
        questionType: null, // 试题类型
        tags: null, // 标签名称
        province: null, // 企业所在地省份
        city: null, // 企业所在城市
        keyword: null, // 关键字
        remarks: null, // 题目备注
        shortName: null, // 企业简称
        direction: null, // 方向
        creatorID: null, // 录入人
        catalogID: null // 目录
      },
      pages: {
        page: 1, // 当前页数
        pagesize: 5 // 页尺寸
      },
      total: 0,
      dialogVisible: false,
      dialogData: {},
      videos: false
    }
  },
  created () {
    this.getQuestions()
  },
  methods: {
    async getQuestions () {
      const obj = { ...this.pages, ...this.questionsSData }
      const { data } = await list(obj)
      console.log(data)
      this.tableData = data.items
      this.total = data.counts
      // console.log(this.total)
      //   data.items.forEach(item => {
      //     item.addDate = dayjs(item.addDate).format('YYYY-MM-DD HH:mm:ss')
      //   })
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
      this.questionsSData.pagesize = val
      this.getQuestions()
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      this.questionsSData.page = val
      this.getQuestions()
    },
    // 搜索
    submitForm (obj) {
      this.questionsSData = obj
      this.getQuestions()
    },
    // 删除
    deleteQuestionsById (obj) {
      console.log(obj)
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await remove(obj)
          this.getQuestions()
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 加入精选题库
    async patchChoiceAdd (obj) {
      obj.choiceState = 1
      // console.log(obj)
      this.$confirm('此操作将该题目加入精选, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await choiceAdd(obj)
          this.getQuestions()
          this.$message({
            type: 'success',
            message: '加入精选成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 预览
    async getDetail (obj) {
      this.dialogVisible = true
      const { data } = await detail(obj)
      console.log(data)
      this.dialogData = data
    }
  },
  components: {
    questionsSearch,
    questionsPreview
  }
}
</script>

<style lang="scss" scoped>
.container {
  height: 100%;

  .box-card {
    height: 100%;
    margin: 10px;

    .el-table {
      margin: 20px 0;
    }

    ::v-deep.el-dialog__footer {
      text-align: right;
    }
  }
}
</style>
